"use strict"

import Vue from "vue"
import Vuex from "vuex"

Vue.use(Vuex)

const state = {
    credentials: {
        authenticated: false
    },
    stsCreds: {},
    toolbar: {
        title: "LayarKu",
        backButton: false
    },
    snackbar: {
        visible: false,
        text: null,
        timeout: 6000,
        multiline: false
    },
    dialogConfirm: {
        visible: false,
        title: "Confirm",
        text: ""
    },
    appAlert: {
        visible: false,
        title: "",
        text: ""
    },
    loading: false
}

const mutations = {
    SET_CREDENTIALS (state, payload) {
        if (payload.email) state.credentials.email = payload.email
        if (payload.access_token) state.credentials.access_token = payload.access_token
        if (payload.access_token_exp) state.credentials.access_token_exp = payload.access_token_exp
        if (payload.refresh_token) state.credentials.refresh_token = payload.refresh_token
        if (payload.refresh_token_exp) state.credentials.refresh_token_exp = payload.refresh_token_exp
        // payload.authenticated is boolean, so we can't use it for checking
        // existence of property. Use 'hasOwnProperty' instead!
        if (Object.prototype.hasOwnProperty.call(payload, "authenticated")) {
            state.credentials.authenticated = payload.authenticated
        }
    },
    SET_STS_CREDS(state, payload) {
        state.stsCreds.aws_access_key_id = payload.aws_access_key_id
        state.stsCreds.aws_secret_access_key = payload.aws_secret_access_key
        state.stsCreds.aws_session_token = payload.aws_session_token
        state.stsCreds.env = payload.env
    },
    SET_TOOLBAR (state, payload) {
        state.toolbar.title = payload.title
        if (Object.prototype.hasOwnProperty.call(payload, "backButton")) {
            state.toolbar.backButton = payload.backButton
        } else {
            state.toolbar.backButton = false
        }
    },
    SHOW_SNACKBAR (state, payload) {
        state.snackbar.text = payload.text
        state.snackbar.multiline = (payload.text.length > 50)?true:false
        if (payload.multiline) {
            state.snackbar.multiline = payload.multiline
        }
        if (payload.timeout) {
            state.snackbar.timeout = payload.timeout
        } else {
            state.snackbar.timeout = 6000
        }
        state.snackbar.visible = true
    },
    CLOSE_SNACKBAR(state) {
        state.snackbar.visible = false
        state.snackbar.multiline = false
        state.snackbar.timeout = 6000
        state.snackbar.text = null
    },
    SET_DIALOG_CONFIRM (state, payload) {
        if (payload.title) state.dialogConfirm.title = payload.title
        if (payload.text) state.dialogConfirm.text = payload.text
        if (Object.prototype.hasOwnProperty.call(payload, "visible")) {
            state.dialogConfirm.visible = payload.visible
        }
    },
    SHOW_APP_ALERT (state, payload) {
        state.appAlert.title = payload.title
        state.appAlert.text = payload.text
        state.appAlert.visible = true
    },
    HIDE_APP_ALERT (state) {
        state.appAlert.visible = false
    },
    SET_LOADING (state, payload) {
        state.loading = payload
    }
}

const actions = {}

export default new Vuex.Store({
    state,
    mutations,
    actions
})
