"use strict"

import { get, getWithAuth, getWithCustomAuth, post,
    postWithAuth } from "./fetch_utils"
import { API_URL, AUTH_URL, REALM } from "@/config"
import { clearCredentials, clearStsCredentials, loadCredentials,
    loadStsCredentials, saveAccessToken, saveCredentials,
    saveStsCredentials } from "./storage" // localStorage related
import store from "./store" // Vuex related
import router from "./router"
import { getTokenExp } from "./utils"
import { setAwsConfig } from './aws_lib'

let refreshingTimerId = null
let sessionEndTimer = null

const END_SESSION_WARNING_TIME = 60 // session end warning time (seconds before session end)
const eswtDelay = 1000 * END_SESSION_WARNING_TIME

function onLogout() {
    if (null !== refreshingTimerId) {
        clearTimeout(refreshingTimerId)
        refreshingTimerId = null
    }
    if (null !== sessionEndTimer) {
        clearTimeout(sessionEndTimer)
        sessionEndTimer = null
    }
    clearCredentials()
    clearCredentials()
}

function onEndSessionTimerTimeout() {
    onLogout()
    router.push("/sessionend")
}

function onEndSessWarnTimeout() {
    store.commit('SHOW_APP_ALERT', {
        title: 'User Session',
        text: 'Your session will end in 1 minute. Please save important work!'
    })
    sessionEndTimer = setTimeout(onEndSessionTimerTimeout, eswtDelay)
}

function calcDelayAndStartRefreshTimer(refreshToken, refreshTokenExpire,
        accessTokenExpire) {
    let d_exp = new Date(1000 * accessTokenExpire)
    let now = new Date()
    let delay = d_exp - now - 60000 // 1 minute early
    if (accessTokenExpire >= (refreshTokenExpire - 60)) {
        sessionEndTimer = setTimeout(onEndSessWarnTimeout, delay-eswtDelay)
    } else {
        startRefreshingTimer(refreshToken, delay)
    }
}

async function refreshAccessToken(refreshToken) {
    let resp = await getWithCustomAuth(`${AUTH_URL}/refresh`, refreshToken)
    if (200 !== resp.code) {
        clearCredentials()
        clearStsCredentials()
        refreshingTimerId = null
        return null
    }
    
    let accessTokenExpire = saveAccessToken(resp.data.access_token)

    resp = await getWithAuth(`${API_URL}/temp_creds`)
    if (200 !== resp.code) {
        clearCredentials()
        clearStsCredentials()
        refreshingTimerId = null
        return null
    }

    saveStsCredentials(resp.data)
    setAwsConfig()
    return accessTokenExpire
}

function startRefreshingTimer(refreshToken, delay) {
    if (null !== refreshingTimerId) clearTimeout(refreshingTimerId)
    refreshingTimerId = setTimeout(
        () => {
            (async function(){
                let _exp = await refreshAccessToken(refreshToken)
                if (null === _exp) return
                calcDelayAndStartRefreshTimer(
                    refreshToken,
                    store.state.credentials.refresh_token_exp,
                    _exp
                )
            })()
        },
        delay
    )
}

export async function register(email, name, password) {
    let params = {
        realm: REALM,
        user_id: email,
        realname: name,
        password: password
    }
    return await post(`${AUTH_URL}/register`, params)
}

export async function verify(token) {
    return await get(`${AUTH_URL}/verify/${token}`)
}

export async function login(email, password, remember) {
    let parameters = {
        realm: REALM,
        user_id: email,
        password: password
    }
    let resp = await post(`${AUTH_URL}/login`, parameters)
    if (resp.code != 200) return resp
    let accessToken = resp.data.access_token
    let refreshToken = resp.data.refresh_token
    saveCredentials(
        email, accessToken, refreshToken, remember
    )
    // Get app spesifice security tokens
    resp = await getWithAuth(`${API_URL}/temp_creds`)
    if (resp.code != 200) {
        onLogout()
        return resp
    }
    let stsCreds = resp.data
    saveStsCredentials(stsCreds)
    setAwsConfig()
    let expire = getTokenExp(accessToken)
    let d_exp = new Date(1000 * expire)
    let now = new Date()
    let delay = d_exp - now - 60000 // 1 minute early
    startRefreshingTimer(refreshToken, delay)
    return resp
}

export async function requestPwResetLink(email) {
    let params = {
        realm: REALM,
        user_id: email
    }
    return await post(`${AUTH_URL}/forgot`, params)
}

export async function resetPassword(token, newpassword) {
    let params = {
        token, newpassword
    }
    return await post(`${AUTH_URL}/resetpwd`, params)
}

export async function chpass(oldpassword, newpassword) {
    let params = {
        oldpassword, newpassword
    }
    return await postWithAuth(`${AUTH_URL}/chpass`, params)
}

// session logout
// using refresh token, since it will be stored in revocation list
export async function logout() {
    let resp = { 'code': 200 }
    if (store.state.credentials.refresh_token) {
        resp = await getWithCustomAuth(
            `${AUTH_URL}/logout`, store.state.credentials.refresh_token
        )
    }
    onLogout()
    return resp
}

export async function globalLogout() {
    // FIXME!
    // Use access token as parameter, since all active refresh token will be revoked.
    let resp = await getWithAuth(`${AUTH_URL}/globallogout`)
    if (resp.code === 200 ) onLogout()
    return resp
}

export async function unregister() {
    // FIXME! needs fresh access token. Use DELETE or POST method
    // TODO: Use access token as parameter, since all active refresh token will be revoked.
    let resp = await getWithCustomAuth(
        `${AUTH_URL}/unregister`, store.state.credentials.refresh_token
    )
    if (200 === resp.code) onLogout()
    return resp
}

export async function onProgramStartUp() {
    let credentials = loadCredentials()
    if (undefined === credentials) return
    
    if (undefined === credentials.access_token) {
        if (undefined === credentials.refresh_token) return
        // Get refreshed access token
        let _exp =  await refreshAccessToken(credentials.refresh_token)
        if (null === _exp) return
        calcDelayAndStartRefreshTimer(
            credentials.refresh_token,
            credentials.refresh_token_exp,
            _exp
        )
    } else {
        // has valid access_token (expiration is handled by loadCredentials())
        loadStsCredentials()
        setAwsConfig()
        if (undefined === credentials.refresh_token) {
            // Start end session timer
            let refreshTokenExp = Math.round((new Date())/1000)
            calcDelayAndStartRefreshTimer(
                undefined,
                refreshTokenExp,
                credentials.access_token_exp
            )
            return
        }
        calcDelayAndStartRefreshTimer(
            credentials.refresh_token,
            credentials.refresh_token_exp,
            credentials.access_token_exp
        )
    }
}
