"use strict"

const ALLOWED_MIME_TYPES = ["video/mp4", "image/jpeg", "image/png"]
const FILE_EXTENSIONS = ['mp4', 'jpg', 'png']

export const WEEKDAYS = ['Senin', 'Selasa', 'Rabu', 'Kamis', 'Jumat', 'Sabtu', 'Minggu']
export const WEEKDAYS_SHORT = ['Sen', 'Sel', 'Rab', 'Kam', 'Jum', 'Sab', 'Min']

export function delay(milis) {
    return new Promise(resolve => {
        setTimeout(() => {resolve()}, milis)
    })
}

export function validateEmail(email) {
    // eslint-disable-next-line
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}

export function getTokenExp(token) {
    let claims = JSON.parse(atob(token.split(".")[1]))
    return claims.exp
}

export function randomString(length) {
   var result           = '';
   var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
   var charactersLength = characters.length;
   for ( var i = 0; i < length; i++ ) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
   }
   return result;
}

export function isAllowedAssetType(mimetype) {
  return (ALLOWED_MIME_TYPES.indexOf(mimetype) >= 0)
}

export function objToUrlQuery (params) {
  var esc = encodeURIComponent
  var query = Object.keys(params)
    .map(k => esc(k) + '=' + esc(params[k]))
    .join('&')
  return query
}

export function fileSizeForHuman(value) {
  if (value >= 1048576) {
    // mega byte
    if ((value % 1048576) == 0) {
      let mb = Math.round(value / 1048576)
      return `${mb} mb`
    } else {
      let mb = (value / 1048576).toFixed(1)
      return `${mb} mb`
    }
  } else if (value >= 1024) {
    // kilo byte
    if ((value % 1024) == 0) {
      let kb = Math.round(value % 1024)
      return `${kb} kb`
    } else {
      let kb = (value / 1024).toFixed(1)
      return `${kb} kb`
    }
  } else {
    return `${value} byte`
  }
}

export function durationForHuman(value) {
  if (value >= 3600) {
    if ((value % 3600) == 0) {
      let hr = Math.round(value/3600)
      return `${hr}h`
    } else {
      let hr = Math.floor(value/3600)
      let remaining = value % 3600
      if ((remaining % 60) == 0) {
        let m = Math.round(remaining / 60)
        return `${hr}h ${m}m`
      } else {
        let m = Math.floor(remaining / 60)
        let s = remaining % 60
        return `${hr}h ${m}m ${s}s`
      }
    }
  } else if (value >= 60) {
    if ((value % 60) == 0) {
      let m = Math.round(value / 60)
      return `${m}m`
    } else {
      let m = Math.floor(value / 60)
      let s = value % 60
      return `${m}m ${s}s`
    }
} else {
    return `${value}s`
  }
}

export function getMediaFileExtension(mediaType) {
  try {
    return FILE_EXTENSIONS[mediaType]
  } catch (error) {
    return 'bin'
  }
}

export function fileExtToAssetType(filename) {
  let a = filename.split('.')[1]
  if (undefined === a) return 999
  let b = a.toLowerCase()
  if ('mp4' === b) return 0
  if ('jpg' === b) return 1
  if ('jpeg' === b) return 1
  if ('png' === b) return 2
  return 999
}

export function pad(number){
  var r = String(number)
  if ( r.length === 1 ) {
      r = '0' + r
  }
  return r
}
