"use strict"

import { REALM } from "@/config"
import store from "./store"
import { getTokenExp } from "./utils"

export function loadCredentials() {
    let credentials = {}
    let isAuthenticated = false
    let expire = null
    let d_expire = null
    let now = new Date()

    let key = `${REALM}:last_user`
    let email = localStorage.getItem(key)
    if (null === email) return undefined

    key = `${REALM}:${email}:atk`
    let _data = localStorage.getItem(key)
    if (null === _data) {
        credentials.access_token = undefined
        credentials.access_token_exp = undefined
    } else {
        expire = getTokenExp(_data)
        d_expire = new Date(1000 * expire)
        if (now > (d_expire - 60)) { // 1 minute earlier than token
            credentials.access_token = undefined
            credentials.access_token_exp = undefined
        } else {
            credentials.access_token = _data
            credentials.access_token_exp = expire
            isAuthenticated = true
        }
    }

    key = `${REALM}:${email}:rtk`
    _data = localStorage.getItem(key)
    if (null === _data) {
        credentials.refresh_token = undefined
        credentials.refresh_token_exp = undefined
    } else {
        expire = getTokenExp(_data)
        d_expire = new Date(1000 * expire)
        if (now > (d_expire - 60)) { // 1 minute earlier than token
            credentials.refresh_token = undefined
            credentials.refresh_token_exp = undefined
        } else {
            credentials.refresh_token = _data
            credentials.refresh_token_exp = expire
        }
    }

    if (isAuthenticated) {
        credentials.email = email
    } else {
        credentials.email = undefined
    }
    let payload = {
        email: credentials.email,
        access_token: credentials.access_token,
        access_token_exp: credentials.access_token_exp,
        refresh_token: credentials.refresh_token,
        refresh_token_exp: credentials.refresh_token_exp,
        authenticated: isAuthenticated
    }
    store.commit("SET_CREDENTIALS", payload)

    return credentials
}

export function saveAccessToken(access_token) {
    let claims = JSON.parse(atob(access_token.split(".")[1]))
    let key = `${claims.aud}:${claims.sub}:atk`
    localStorage.setItem(key, access_token)
    store.commit("SET_CREDENTIALS", {
        email: claims.sub,
        access_token,
        access_token_exp: claims.exp,
        authenticated: true
    })
    return claims.exp
}

export function saveCredentials(email, access_token, refresh_token, remember) {
    let key = `${REALM}:${email}:atk`
    localStorage.setItem(key, access_token)
    let access_token_exp = getTokenExp(access_token)
    let refresh_token_exp = getTokenExp(refresh_token)
    if (remember) {
        key = `${REALM}:${email}:rtk`
        localStorage.setItem(key, refresh_token)
    }
    key = `${REALM}:last_user`
    localStorage.setItem(key, `${email}`)

    let payload = {
        email: email,
        access_token: access_token,
        access_token_exp: access_token_exp,
        refresh_token: refresh_token,
        refresh_token_exp: refresh_token_exp,
        authenticated: true
    }
    store.commit("SET_CREDENTIALS", payload)
}

export function clearCredentials() {
    let email = store.state.credentials.email
    let key = `${REALM}:${email}:atk`
    localStorage.removeItem(key)
    key = `${REALM}:${email}:rtk`
    localStorage.removeItem(key)
    key = `${REALM}:last_user`
    localStorage.removeItem(key)

    let payload = {
        email: undefined,
        access_token: undefined,
        access_token_exp: undefined,
        refresh_token: undefined,
        refresh_token_exp: undefined,
        env: undefined,
        authenticated: false
    }
    store.commit("SET_CREDENTIALS", payload)
}

export function saveStsCredentials(stsCreds) {
    let email = store.state.credentials.email
    let key = `${REALM}:${email}:sts`
    localStorage.setItem(key, JSON.stringify(stsCreds))
    store.commit("SET_STS_CREDS", stsCreds)
}

export function clearStsCredentials() {
    let email = store.state.credentials.email
    let key = `${REALM}:${email}:sts`
    localStorage.removeItem(key)
    let payload = {
        aws_access_key_id: undefined,
        aws_secret_access_key: undefined,
        aws_session_token: undefined,
        env: undefined
    }
    store.commit("SET_STS_CREDS", payload)
}

export function loadStsCredentials() {
    let email = store.state.credentials.email
    let key = `${REALM}:${email}:sts`
    let payload = localStorage.getItem(key)
    if (null === payload) return
    let stsCreds = JSON.parse(payload)
    store.commit("SET_STS_CREDS", stsCreds)
}