<template>
  <v-app>
    <component v-bind:is="layout"></component>
  </v-app>
</template>

<script>
"use strict"

import store from "./lib/store"
import AppLayout from "./components/AppLayout"
import SimpleLayout from "./components/SimpleLayout"
import mqtt from '@/lib/mqtt_client.js'

export default {
  name: 'App',
  components: {
    "simple-layout": SimpleLayout,
    "app-layout": AppLayout
  },
  computed: {
    layout: () => store.state.credentials.authenticated?"app-layout":"simple-layout"
  },
  created() {
    mqtt.init()
  },
  data: () => ({
    //
  }),
};
</script>
