"use strict"

import store from "./store"

export async function post(url, params) {
    let options = {
        method: "POST",
        headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
        },
        body: JSON.stringify(params)
    }
    try {
        let resp = await fetch(url, options)
        resp = await resp.json()
        return resp
    } catch(error) {
        return { code: 600, message: error.message }
    }
}

export async function postWithAuth(url, params) {
    let options = {
        method: "POST",
        headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
            Authorization: store.state.credentials.access_token
        },
        body: JSON.stringify(params)
    }
    try {
        let resp = await fetch(url, options)
        resp = await resp.json()
        return resp
    } catch(error) {
        return { code: 600, message: error.message }
    }
}

