<template>
  <v-container fluid class="pa-0 pa-md-3">
    <v-list v-if="0!==players.length" class="ly-list">
      <template v-for="(player, index) of players">
        <v-list-item :key="player[0]">
          <v-row no-gutters>
            <v-col cols="12" md="5">
              {{ player[1]===''?`Player ${player[0]}`:player[1] }}
            </v-col>
            <v-col cols="11" md="5" offset="1" offset-sm="0">
              {{ player[3] }}
            </v-col>
            <v-col cols="6" md="2" offset="6" offset-sm="0" class="text-right">
              <!-- v-icon>mdi-check</v-icon>
              <v-icon>mdi-clear</v-icon -->
              <v-btn icon @click.stop="onDeleteClick(player, index)">
                <v-icon>mdi-delete-forever</v-icon>
              </v-btn>
              <v-btn icon @click="onChGroupClick(player, index)">
                <v-icon>mdi-select-group</v-icon>
              </v-btn>
              <v-btn icon @click="onEditClick(player, index)">
                <v-icon>mdi-pencil</v-icon>
              </v-btn>
            </v-col>
          </v-row>
        </v-list-item>
        <v-divider :key="index" v-if="index!==players.length-1"></v-divider>
      </template>
    </v-list>
    <v-row v-if="(0===players.length) && pageReady" class="centered-row">
      <v-col cols="12" md="6">
        <v-card>
          <v-card-title>Belum ada media player!</v-card-title>
          <v-card-text>
            Tambahkan media player dengan meng-klik 
            tombol yang ada di sudut kanan-bawah layar!
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
    <v-btn fab fixed bottom right dark color="pink" @click="onBtnAddClick">
      <v-icon>mdi-plus</v-icon>
    </v-btn>
    <v-dialog v-model="addDialog" max-width="360" persistent>
      <v-card>
        <v-card-title>Registrasi Media Player</v-card-title>
        <v-card-text>
          <v-text-field label="Nomor PIN" placeholder="Nomor PIN yang tampil di layar"
            v-model="regCode"></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="addDialog=false">Batal</v-btn>
          <v-btn text color="primary" @click="onBtnAddOkClick" :disabled="btnOkRegCodeDisabled">Ok</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="editDialog" max-width="360" persistent>
      <v-card>
        <v-card-title>Edit Media Player</v-card-title>
        <v-card-text>
          <v-text-field label="Nama media player" v-model="editNameText"></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="editDialog=false">Batal</v-btn>
          <v-btn text color="primary" @click="onConfirmEdit" :disabled="btnOkChNameDisabled">Ok</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="chGroupDialog" max-width="360">
      <v-card>
        <v-list>
          <template v-for="(group, index) in groups">
            <v-list-item :key="group[0]" @click="changeGroup(group[0], group[1])">
              <v-list-item-content>{{ group[1] }}</v-list-item-content>
            </v-list-item>
            <v-divider :key="index" v-if="index!==(groups.length-1)"></v-divider>
          </template>
        </v-list>
      </v-card>
    </v-dialog>
    <v-dialog max-width="320" v-model="dlgConfirmDel">
      <v-card>
        <v-card-title>Konfirmasi</v-card-title>
        <v-card-text>
          Hapus {{ playerNameToBeDel }}?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="dlgConfirmDel=false" :disabled="loading">Batal</v-btn>
          <v-btn text color="warning" :disabled="loading" @click="deletePlayer">Ok</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
"use strict"

import { deleteWithAuth, getWithAuth, postWithAuth } from '@/lib/fetch_utils'
import { API_URL } from '@/config'
import mqtt from '@/lib/mqtt_client.js'

export default {
  name: "Players",
  data: () => ({
    addDialog: false,
    chGroupDialog: false,
    chGroupPlayerIndex: -1,
    dlgConfirmDel: false,
    editDialog: false,
    editNameIndex: -1,
    editNameText: '',
    groups: [],
    loading: false,
    nowTs: 0,
    pageReady: false,
    playerIdToBeDel: '',
    playerIndexToBeDel: -1,
    playerNameToBeDel: '',
    players: [],
    regCode: '',
  }),
  computed: {
    btnOkRegCodeDisabled(){
      if (this.regCode.length == 0) return true
      if (this.loading) return true
      return false
    },
    btnOkChNameDisabled(){
      if (this.editNameText.length === 0) return true
      if (this.loading) return true
      return false
    }
  },
  watch: {
    loading: function() {
      this.$store.commit('SET_LOADING', this.loading)
    }
  },
  created() {
    this.$store.commit("SET_TOOLBAR", {title:"Media Player"})
    if (!mqtt.connected) {
      mqtt.connect()
    }
    this.loadData()
  },
  beforeDestroy() {
    if (mqtt.connected) mqtt.disconnect()
  },
  methods: {
    loadData: async function() {
      let resp = await getWithAuth(`${API_URL}/players`)
      if (resp.code === 200) {
        this.players = resp.data.rows
      }
      this.pageReady = true
    },
    onChGroupClick: async function(player, index) {
      this.chGroupPlayerIndex = index
      if (this.groups.length === 0) {
        this.loading = true
        let resp = await getWithAuth(`${API_URL}/groups`)
        this.loading = false
        if (resp.code !== 200) {
          this.$store.commit('SHOW_APP_ALERT', {
            title: 'Error',
            text: resp.message
          })
          return
        }
        this.groups = resp.data.rows
        this.chGroupDialog = true
      } else {
        this.chGroupDialog = true
      }
    },
    onDeleteClick(player, index) {
      this.playerIdToBeDel = player[0]
      this.playerIndexToBeDel = index
      this.playerNameToBeDel = player[1]===''?`Player ${player[0]}`:player[1]
      this.dlgConfirmDel = true
    },
    onEditClick(player, index) {
      this.editNameText = player[1]===''?`Player ${player[0]}`:player[1]
      this.editNameIndex = index
      this.editDialog = true
    },
    onConfirmEdit: async function() {
      if (this.editNameText.length === 0) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: 'Nama media player tidak sah!'
        })
        return
      }
      let player = this.players[this.editNameIndex]
      this.loading = true
      let response = await postWithAuth(
        `${API_URL}/players/${player[0]}`, {name: this.editNameText}
      )
      this.loading = false
      this.editDialog = false
      if (response.code !== 200) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: response.message
        })
        return
      }
      player[1] = this.editNameText
    },
    onBtnAddClick () {
      this.regCode = ''
      this.addDialog = true
    },
    onBtnAddOkClick: async function() {
      if (this.regCode.length === 0) {
        this.addDialog = false
        return
      }
      this.loading = true
      let response = await postWithAuth(
        `${API_URL}/players`, {regcode: this.regCode}
      )
      this.loading = false
      this.addDialog = false
      if (response.code !== 200) {
        let message = `ERROR CODE: ${response.code}`
        if (response.code === 404) message = 'Invalid PIN'
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: message
        })
        return
      }
      response = await getWithAuth(`${API_URL}/players/${response.data}`)
      if (response.code !== 200) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: response.message
        })
        return
      }
      let player = [
        response.data.id,
        response.data.name,
        response.data.pgroup,
        response.data.group_name
      ]
      this.players.unshift(player)
    },
    changeGroup: async function(groupId, groupName) {
      let player = this.players[this.chGroupPlayerIndex]
      this.loading = true
      let response = await postWithAuth(
        `${API_URL}/players/${player[0]}`, {pgroup: groupId}
      )
      this.loading = false
      this.chGroupDialog = false
      if (response.code !== 200) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: response.message
        })
        return
      }
      player[2] = groupId
      player[3] = groupName
      mqtt.notifyDevice(player[0])
    },
    deletePlayer: async function(){
      this.loading = true
      let response = await deleteWithAuth(
        `${API_URL}/players/${this.playerIdToBeDel}`
      )
      this.loading = false
      this.dlgConfirmDel = false
      if (response.code !== 200) {
        this.$store.commit('SHOW_APP_ALERT', {
          title: 'Error',
          text: response.message
        })
        return
      }
      mqtt.notifyDevice(this.playerIdToBeDel)
      // eslint-disable-next-line
      let _p = this.players.splice(this.playerIndexToBeDel, 1)
    }
  }
}
</script>

<style scoped>
.ly-list {
  margin-bottom: 64px;
}
</style>
