'use strict'

import AWS from 'aws-sdk/global'
import * as sigv4 from '@/lib/sigv4'
import store from '@/lib/store'

const INTERVAL = 3600000 // 60 minutes
//~ const GRACE = 60000 // 1 minute

export function setAwsConfig() {
    let stsCreds = store.state.stsCreds
    let env = stsCreds.env
    AWS.config.region = env.s3_region
    AWS.config.s3 = {
        endpoint: env.s3_endpoint_url,
        s3ForcePathStyle: false
    }
    AWS.config.accessKeyId = stsCreds.aws_access_key_id
    AWS.config.secretAccessKey = stsCreds.aws_secret_access_key
    AWS.config.sessionToken = stsCreds.aws_session_token
    AWS.config.signatureVersion = 'v4'
    AWS.config.s3ForcePathStyle = false
}

