export function s3PreSignWCache(bucket, key, expires) {
  let nowTs = Date.now()
  let baseTime = nowTs - (nowTs % INTERVAL) // tail
  let creds = store.state.stsCreds
  let env = creds.env
  let _h = env.s3_endpoint_url.split('://')
  let protocol = _h[0]
  let host = bucket + '.' + _h[1]
  let path = '/' + key
  let url = sigv4.createPresignedURL('GET', host, path, 's3', 
      "UNSIGNED-PAYLOAD", {
    key: creds.aws_access_key_id,
    secret: creds.aws_secret_access_key,
    sessionToken: creds.aws_session_token,
    protocol: protocol,
    timestamp: baseTime,
    region: env.s3_region,
    expires: expires,
    signSessionToken: true
  })
  return url
}
