"use strict"

import Vue from "vue"
import VueRouter from "vue-router"

import { logout } from "./auth"
import store from "./store"

//~ import Home from "@/views/Home"
import Players from '@/views/Players'

Vue.use(VueRouter)

async function onLogout(to, _from, next) {
    await logout()
    next("/login")
}

const routes = [
    {
        path: "/",
        name: "home",
        //~ component: Home,
        component: Players,
        meta: { requiresAuth: true }
    },
    {
        path: "/groups",
        name: "groups",
        component: () => import(/* webpackChunkName: "groups" */ '@/views/Groups')
    },
    {
        path: "/assets",
        name: "assets",
        component: () => import(/* webpackChunkName: "assets" */ '@/views/Assets')
    },
    {
        path: "/playlists",
        name: "playlists",
        component: () => import(/* webpackChunkName: "playlists" */ '@/views/Playlists')
    },
    {
        path: "/playlist_form",
        name: "playlist_form",
        component: () => import(/* webpackChunkName: "playlist_form" */ '@/views/PlaylistForm')
    },
    {
        path: "/shows",
        name: "shows",
        component: () => import(/* webpackChunkName: "shows" */ '@/views/Shows')
    },
    {
        path: "/show_form",
        name: "show_form",
        component: () => import(/* webpackChunkName: "shows" */ '@/views/ShowForm')
    },
    {
        path: "/register",
        name: "register",
        component: () => import(/* webpackChunkName: "register" */ '@/views/Register')
    },
    {
        path: "/verify/:token",
        name: "verify",
        component: () => import(/* webpackChunkName: "verify" */ '@/views/Verify')
    },
    {
        path: "/login",
        name: "login",
        component: () => import(/* webpackChunkName: "login" */ '@/views/Login')
    },
    {
        path: "/forgot",
        name: "forgot",
        component: () => import(/* webpackChunkName: "forgot" */ '@/views/Forgot')
    },
    {
        path: "/resetpwd/:token",
        name: "resetpwd",
        component: () => import(/* webpackChunkName: "resetpwd" */ '@/views/ResetPwd')
    },
    {
        path: "/chpass",
        name: "chpass",
        component: () => import(/* webpackChunkName: "chpass" */ '@/views/ChPass'),
        meta: { requiresAuth: true }
    },
    {
        path: "/account",
        name: "account",
        component: () => import(/* webpackChunkName: "account" */ '@/views/Account'),
        meta: { requiresAuth: true }
    },
    {
        path: "/about",
        name: "about",
        component: () => import(/* webpackChunkName: "about" */ '@/views/About'),
        meta: { requiresAuth: true }
    },
    {
        path: "/logout",
        name: "logout",
        beforeEnter: onLogout
    },
    {
        path: "/sessionend",
        name: "sessionend",
        component: () => import(/* webpackChunkName: "sessionend" */ '@/views/SessionEnd')
    },
    {
        path: "/goodbye",
        name: "goodbye",
        component: () => import(/* webpackChunkName: "goodbye" */ '@/views/Goodbye')
    }
]

const router = new VueRouter({
    routes
})

router.beforeEach((to, _from, next) => {
    if (to.matched.some(record => record.meta.requiresAuth)) {
        // This route requires authentication
        if (store.state.credentials.authenticated) {
            next()
        } else {
            next({
                path: '/login',
                query: { redirect: to.fullPath }
            })
        }
    } else {
        if (to.name === 'login') {
            if (store.state.credentials.authenticated) {
                next('/')
            } else {
                next()
            }
        } else {
            next()
        }
    }
})

export default router
