'use strict'

import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify'
import router from './lib/router'
import store from './lib/store'
import { onProgramStartUp } from '@/lib/auth'

Vue.config.productionTip = false

async function appInit() {
  await onProgramStartUp()
  new Vue({
    router,
    store,
    vuetify,
    render: h => h(App)
  }).$mount('#app')
  let openingLogo = document.getElementById('logo-loading')
  openingLogo.parentNode.removeChild(openingLogo)
}

appInit()
