'use strict'

import PahoMQTT from 'paho-mqtt'

import { MQTT_BROKER, MQTT_PATH, MQTT_PORT } from '@/config.js'
import { randomString } from './utils.js'

const mqtt = {
    client: null,
    clientHandle: null,
    timerHandle: null,
    connected: false,
}

mqtt.init = function() {
    this.clientHandle = randomString(8)
    this.client = new PahoMQTT.Client(
        MQTT_BROKER, MQTT_PORT, MQTT_PATH, this.clientHandle
    )
    this.client.onConnected = this.onConnected.bind(this)
    this.client.onConnectionLost = this.onConnectionLost.bind(this)
    //~ this.client.onMessageArrived = this.onMessageArrived.bind(this)
}

mqtt.connect = function() {
    let connectionOpts = {
        cleanSession: true,
        useSSL: true,
        keepAliveInterval: 10,
        reconnect: true
    }
    this.client.connect(connectionOpts)
}

mqtt.disconnect = function() {
    //~ this.client.unsubscribe(`lyinfosrv/p/${this.clientHandle}`)
    //if (this.connected) { // is it necessary?
        this.client.disconnect()
        this.connected = false
    //}
}

// eslint-disable-next-line
mqtt.onConnected = function(reconnect, URI) {
    this.connected = true
    //~ this.client.subscribe(`lyinfosrv/p/${this.clientHandle}`)
}

mqtt.onConnectionLost = function(responseObject) {
    if (0 !== responseObject.errorCode) {
        console.debug(
            'onConnectionLost:',
            responseObject.errorCode,
            responseObject.errorMessage
        )
    }
    // errorCode 4 is when app can not connect to MQTT broker
    
    this.connected = false
}

