mqtt.send = function(topic, payload) {
    if (!this.connected) return
    this.client.send(topic, payload)
}

mqtt.notifyDevice = function(deviceId) {
    if (!this.connected) return
    let deviceHandle = ''
    if (deviceId.length > 8) {
        deviceHandle = deviceId.substr(0, 8)
    } else {
        deviceHandle = deviceId
    }
    let topic = `lyinfosrv/p/${deviceHandle}`
    let payload = JSON.stringify({cmd: 'refresh'})
    this.client.send(topic, payload)
}

export default mqtt
